<template>
    <div class="wrap" v-bind:class="[clickActionNavi ? 'activeNavi' : '']">
        <header>
            <slot name="wrap_title"></slot>
            <div class="wrap_header_btn">
                <slot name="wrap_header_btn">
                    <a class="btn_icon btn_my"> <!-- button icon type class "btn_icon", 대체 텍스트 : span -->
                        <span>내 정보 바로가기</span>
                    </a>
                    <a class="btn_icon btn_menu" @click="addClassActiveNavi"> <!-- button icon type class "btn_icon", 대체 텍스트 : span -->
                        <span>메뉴 바로가기</span>
                    </a>
                </slot>
            </div>

        </header>
        <slot name="container"></slot>
        <Navi @actionRemoveNaviActive="evntRemoveNaviActive"></Navi>
    </div>
    
</template>

<script>
    import Navi from "@/components/Gnb/Navi"
    export default {
        name: "MainLayout",
        components: {
            Navi
        },
        data() {
            return {
                clickActionNavi : false
            }
        },
        methods: {
            addClassActiveNavi() {
                this.clickActionNavi = !this.clickActionNavi;
            },
            evntRemoveNaviActive() {
                this.clickActionNavi = false
            }
        }
    }
</script>

<style scoped>
    /*Main*/
    .main header { position: fixed; z-index: 10; width: 100vw; }


    /*Sub*/


    /*Common*/
    .common header { position: relative; width: 100%; }
</style>